.profile-wrapper {
  overflow: hidden;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  overflow-y: hidden;
  height: 100vh;
}

.profile-contant {
  width: 100%;
  height: 100%;
  max-width: 700px;
  display: flex;
  justify-content: center;
  overflow-y: auto;
}
